<template>
	<view class="body">
		<view class="tabbar">
			<u-tabs :list="list1" @click="click" :scrollable="false" lineColor="#fff" lineWidth="40"></u-tabs>
		</view>
		<view class="starList">
			<!-- 单词列表 -->
			<view class="">
				<view v-for="(item,index) in data" :key="index" class="express_content">
					<view>
						<view class="h5">{{item.word}}</view>
						<view class="h6">{{item.explain}}</view>
					</view>
					<u-icon name="star-fill" color="#ffd75c" size="48rpx" @click="isStar(item)"></u-icon>
				</view>
				<view class="tip" v-if="data<1">
					暂无收藏哦~
				</view>
			</view>
		</view>
	</view>
</template>

<script>
	import store from '@/store/index.js'; //引入store
	import { myCollection, setCollection } from '@/common/http.api.js';
	export default {
		data() {
			return {
				current:'', //当前选择索引
				token:'',
				list1: [{
					name: '简单句',
				}, {
					name: '主从复合句',
				}, {
					name: '并列复合句',
				}, {
					name: '182句真题练习',
				}],
				data:{}
			}
		},
		onLoad() {
			this.token = store.state.vuex_token 
			this.current = 1
			myCollection({token:this.token, type: this.current}).then(res=>{
				console.log(res)
				this.data = res.data.data.rare_word
			}).catch(err=>{
				console.log(err)
			})
		},
		methods: {
			// 切换tabar
			click(item) {
				console.log('item', item);
				this.current = item.index+1
				myCollection({token:this.token, type:this.current}).then(res=>{
					console.log(res)
					this.data = res.data.data.rare_word
				}).catch(err=>{
					console.log(err)
				})
			},
			// 取消收藏
			isStar(item) {
				setCollection({token:this.token, rare_word_id:item.id}).then(res=>{
					console.log(res)
					this.$u.toast('取消收藏成功')
					myCollection({token:this.token, type:this.current}).then(res=>{
						console.log(res)
						this.data = res.data.data.rare_word
					}).catch(err=>{
						console.log(err)
					})
				}).catch(err=>{
					console.log(err)
				})
			}
		}
	}
</script>

<style lang="scss">
	.body{
		.tabbar{
			padding-right:40rpx;
			background-color: #ffd75c;
		}
		.starList{
			padding: 30rpx;
			.tip{
				text-align: center;
			}
			.express_content{
				display: flex;
				justify-content: space-between;
				margin: 20rpx 0;
			}
			.h5 {
				font-size: 32rpx;
				font-weight: 500;
				line-height: 48rpx;
			}
			.h6 {
				font-size: 24rpx;
				line-height: 36rpx;
				color: #999;
				font-weight: 400;
				margin-bottom: 20rpx;
			}
		}
		.u-tabs__wrapper__nav__item__text{
			font-size: 26rpx !important;
			margin-bottom: 20rpx;
		}
		.u-tabs__wrapper__nav__item{
			padding: 0 !important;
		}
	}
</style>